import styled from 'styled-components'
import { registrarIVA } from '../../api/civilo_roller_api';
import { useState, useEffect } from 'react';

const Formulario = styled.form`
    background-color: #f8f8f8;
    border-radius: 3px;
    border: solid;
    border-color: gray;
    display: flex;
    flex-direction: column;
    margin: auto; //Se centra el formulario
    margin-top: 15px;
    margin-bottom: 15px;
    width: 43%;
    word-wrap: break-word; //Hace que el texto se ajuste de forma automtica para evitar que se salga del botn.

    @media (max-width: 950px){
      width: 85%;
    }
    @media (max-width: 500px){
      width: 90%;
    }


`;

const EspacioVertical = styled.div`
  margin-bottom: 10px;
`;

const Titulo = styled.div`
  font-size: xx-large;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  margin-left: 10%;
`;

const Subtitulo = styled.p`
width: 90%;
color: #a09e9e;
font-size: medium;
border-bottom: 2px solid #a09e9e; /* Lnea separadora */
`;

const Input = styled.input`
  background-color: #ccd0d5;
  border-radius: 6px;
  display: flex;
  width: 79%;
  margin: auto;
`;

const SubmitButton = styled.button`
  background-color: #1f618d;
  border-radius: 10px;
  border-color: #1f618d;
  color: #eae1e1;
  height: 80%;
  font-size: 150%;
  margin: auto;
  margin-bottom: 3%;
  width: 80%;

  //Animacin para cuando el cursor pase por encima del botn.
  &:hover {
      box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.25); /* Agrega una sombra */
      transform: scale(0.95); /* Reduzca ligeramente el tamao */
    }

  // Agrega una sombra cuando el botn es presionado
  &:active {
      box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.25); /* Agrega una sombra ms grande */
      transform: scale(0.9); /* Reduce el tamao an ms */
  }

  @media (max-width: 590px){
    width: 70%;
    
  }

`;

export const AdminIVARegister = () => {
  // Define los estados para cada campo del formulario
  const [porcentajeIVA, setPorcentajeIVA] = useState('');

  const handleClick = (event) => {

    //Se llama al metodo para evitar que haga su comportamiento por defecto, en este caso enviar el formulario
    event.preventDefault();

    //Se guardan los datos del nuevo usuario para enviarlos al servidor
    const usuarioNuevo = {
      "ivaPercentage": porcentajeIVA,
    }

    registrarIVA(usuarioNuevo);
  }
  return (



    <Formulario>
      <EspacioVertical />
      <Titulo>Nuevo IVA
        <Subtitulo>Rellena los datos</Subtitulo>
      </Titulo>

      

      {/*Espacio creado para separar los elementos*/}
      <EspacioVertical />

      <Input type="number" name="porcentajeIVA" placeholder='XX%' onChange={(e) => setPorcentajeIVA(e.target.value)} />
      <EspacioVertical />
    

      <EspacioVertical />
      <SubmitButton onClick={handleClick}>Registrar</SubmitButton>
      {/* lo ultimo que hice fue configurar que al presionar el boton no se haga nada, pq antes se hacia eso de enviar la info al swervidor
    pero ahora no hace nada, por lo que yo puedo configurar lo que se hace*/}
    </Formulario>




  )
}
