import React, { useState } from 'react'
import styled from 'styled-components'

const StyledDiv = styled.div`
    background-color: #d0d3d4;
    border: solid;
    border-color:  #7b7d7d;
    display: flex; //Con display: flex y flex-direction: column, estableces que los elementos hijos de StyledDiv se ordenen en una sola columna
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    width: 250px;

`;

const Titulo = styled.h1`
    color: black;
    font-size: large;
`;

const Imagen = styled.img`
    width: 180px; /* Reduce el tamao de la imagen para que se ajuste dentro del espacio delimitado por el padding */
    height: 220px;
    object-fit: contain; /* Ajusta la imagen dentro de su contenedor */

`;

const Descripcion = styled.p`
    color: black;
    font-size: medium;
    max-width: 100%; /* Ajustamos el ancho mximo de la descripcin al 100% */
    word-wrap: break-word; /* Permitimos que las palabras se dividan en varias lneas si exceden el ancho disponible */
    overflow: hidden; /* Ocultamos cualquier contenido que exceda el tamao fijo del componente */
    //text-overflow: ellipsis; /* Agregamos puntos suspensivos (...) al final del texto si se recorta debido al tamao fijo */
    //white-space: nowrap; /* Evitamos que el texto se divida en varias lneas */
`;

export const ProductItem = ({title,image,description}) => {

    

    return (
        
        <StyledDiv>
            <Titulo>{title}</Titulo>
            <Imagen src={image}/>
            <Descripcion>{description}</Descripcion>
        </StyledDiv>
    )
}
